<template>
  <section
    id="features"
    class="home-features"
    ref="homeFeatures"
    v-waypoint="{
      active: true,
      callback: onWaypoint,
      options: intersectionOptions
    }"
  >
    <!-- Navbar Component -->
    <Navbar v-if="isVisible" :class="{ nfixed: isVisible }" />
    <h1>FEATURES</h1>
  </section>
</template>

<script>
import Navbar from "@/components/home/Navbar";
export default {
  name: "Features",
  data() {
    return {
      isVisible: false,
      intersectionOptions: {
        root: null,
        // rootMargin: "0px 0px 0px 0px",
        threshold: [0.9] // [0.25, 0.75] if you want a 25% offset!
      } // https://developer.mozilla.org/en-US/docs/Web/API/Intersection_Observer_API
    };
  },
  components: {
    Navbar
  },
  methods: {
    onWaypoint({ going, direction }) {
      // going: in, out
      // direction: top, right, bottom, left
      if (going === this.$waypointMap.GOING_IN) {
        // console.log("waypoint going IN");
        this.isVisible = true;
      }
      if (direction === this.$waypointMap.DIRECTION_BOTTOM) {
        // console.log("waypoint going BOTTOM!");
        this.isVisible = false;
      }
    }
  }
};
</script>

<style></style>
